::-webkit-scrollbar {
    width: 8px;
}
::-webkit-scrollbar-track {
    background-color: rgb(33, 33, 33);
}
::-webkit-scrollbar-thumb {
    background: #919191;
}

.expanded-navbar-container {
    position: fixed;
    display: inline-flex;
    flex-direction: column;
    height: 662px;
    width: 245px;
    background-color: rgba(33, 33, 33, 0.985);
    bottom: 0;
    color: white;
    overflow-y: scroll;
    left: 0;
}
.main-section {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
}
.expanded-nav-item {
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 100%;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
}
.expanded-nav-item:hover {
    background-color: rgba(255, 255, 255, 0.2)
}
.expanded-nav-icon {
    margin-right: 26px;
    margin-left: 26px;
    color: #909090;
    font-size: 25px;
}
.divider {
    border-bottom: 1px solid #5c5c5c7c;;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}
.top-divider {
    width: 100%;
    margin-top: 5px;
}
.expanded-nav-header {
    height: 25px;
    padding-top: 5px;
}
.expanded-nav-header-text {
    margin-left: 26px;
    font-size: 14px;
    color: #AAAAAA;
    font-weight: bolder;
}
.expanded-nav-footer {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    list-style: none;
    margin-left: 26px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 0;
}
.section1 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 10px;
    width: 180px;
}
.section2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.expanded-nav-footer-item {
    padding: 1px;
    margin-right: 7px;
    font-size: 13px;
    color: #AAAAAA;
    font-weight: bolder;
    cursor: pointer;
}
.copyright {
    font-size: 13px;
    color: #919191;
    margin-top: 3px;
}