.video-container {
    display: flex;
    margin: 0px 10px 25px 10px;
    height: 270px;
    width: 330px;
    cursor: pointer;
}
.video-container a {
    text-decoration: none;
}
.thumbnail-container {
    display: flex;
    height: 185px;
    width: 330px;
    position: relative;
    margin-bottom: 10px;
}
.hover-btn-container{
    position: absolute;
    right: 0;
    top: 0;
    margin: 5px 5px 0px 0px;
    display: flex;
    flex-direction: column;
}
.iframe{
    display: flex;
    height: 185px;
    width: 330px;
    position: absolute;
}
.iframe-hidden{
    display: hidden;
    height: 185px;
    width: 329px;
    position: absolute;
    z-index: -1;
}
.hover-btns{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    width: 30px;
    background-color: rgba(0,0,0,0.8);
    color: white;
    font-size: 22px;
    margin-bottom: 5px;
}
.timestamp {
    height: 20px;
    width: 50px;
    background-color: black;
    color: white;
    font-size: 12px;
    padding: 3px;
    border-radius: 1px;
}
.timestamp-container {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 0px 3px 3px 0px;
}
.details-container {
    display: flex;
    flex-direction: row;
    height: 75px;
    width: 330px;
    align-items: flex-start;
    position: relative;
}
.channel:hover{
    color: white;
}
.channel-thumbnail {
    margin-right: 10px;
}
.video-title {
    color: white;
    margin: 0;
    font-size: 14px;
    margin-bottom: 5px;
    max-width: 245px;
    max-height: 36px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.video-title a{
    color: white;
}
.video-details h4 {
    margin: 0;
    font-size: 14px;
    color: #AAAAAA;
    font-weight: 500;
}
.video-menu-btn {
    position: absolute;
    right: 0;
}
.menu-btn {
    font-size: 10px;
    color: #919191;
}
.menu-btn:hover {
    color: white;
}
.video-options {
    background-color: rgb(33,33,33);
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    width: 250px;
    position: absolute;
    right: 10px;
    z-index: 1;
    text-overflow: clip;
}
.video-nav-item {
    display: flex;
    flex-direction: row;
    height: 35px;
    width: 100%;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    color: white;
}
.video-nav-item:hover {
    background-color: rgba(255, 255, 255, 0.2)
}
.video-nav-icon{
    margin-right: 15px;
    margin-left: 15px;
    color: #909090;
    font-size: 25px;
}
.top-divider {
    width: 100%;
    margin-top: 5px;
}