.top-navbar-container {
    position: fixed;
    height: 61px;
    width: 100%;
    background-color: rgba(33, 33, 33, 0.985);
    margin-left: 75px;
    display: flex;
    margin: 0;
    border-bottom: 1px solid #5c5c5c7c;
}
.nav-button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    height: 60px;
    font-size: 10px;
    text-align: center;
    color: #909090;
    cursor: pointer;
    margin-left: 27px;
    margin-right: 27px;
}
.bar {
    height: 2px;
    width: 20px;
    margin: 1.5px;
    background-color: white;
    align-self: center;
}
.logo-container {
    display: flex;
    align-content: center;
    height: 60px;
    width: 80px;
    margin-right: auto;
}
.ytlogo{
    display: flex;
    align-self: center;
    width: 78px;
}
.yt-link {
    display: flex;
    align-self: center;
}
.search-container {
    display: flex;
    align-items: center;
    margin: 0 auto;
    width: 650px;
}
.search-input {
    display: flex;
    width: 100%;
    height: 30px;
    border: none;
    background-color: #121212;
    font-size: 16px;
    border: 1px solid #303030;
    margin-right: 0.5px;
    color: white;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}
.search-btn {
    background-color: #303030;
    width: 65px;
    border: none;
    text-decoration: none;
    height: 34px;
    margin-right: 10px;
    border: 1px solid #303030;
    cursor: pointer;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}
#search-icon {
    color: rgba(255,255,255,0.5);
    font-size: 20px;
    width: 55px;
}
#search-icon:hover {
    color: rgb(255, 255, 255)
}
.create-container{
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-right: 20px;
    margin-left: auto;
}
.create-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    padding: 0;
    align-items: center;
    align-content: center;
    align-self: center;
}
.create-nav-item{
    display: flex;
    justify-content: center;
    width: 50px;
    height: 90%;
    align-items: center;
}
.profile-pic {
    margin: 0;
    cursor: pointer;
    border-radius: 50%;
}
.create-mini-nav {
    background-color: rgb(33,33,33);
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    width: 170px;
    position: absolute;
    top: 50px;
    right: 190px;
    border-left: 1px solid #919191;
    border-right: 1px solid #919191;
    border-bottom: 1px solid #919191;
}
.apps-mini-nav {
    background-color: rgb(33,33,33);
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    width: 210px;
    position: absolute;
    top: 50px;
    right: 130px;
    border-left: 1px solid #919191;
    border-right: 1px solid #919191;
    border-bottom: 1px solid #919191;
}
.create-mini-nav-item {
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 100%;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    color: white;
}
.create-mini-nav-item:hover {
    background-color: rgba(255, 255, 255, 0.2)
}
.mini-nav-icon{
    margin-right: 15px;
    margin-left: 15px;
    font-size: 25px;
    color: red;
}