.side-navbar-container {
    position: fixed;
    display: inline-flex;
    flex-direction: column;
    height: 662px;
    width: 75px;
    background-color: rgba(33, 33, 33, 0.985);
    margin: 0;
    bottom: 0;
    z-index: 10000;
}
.side-navbar-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.side-nav-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    height: 70px;
    font-size: 10px;
    text-align: center;
    color: #909090;
    cursor: pointer;
}
.side-nav-item:hover {
    background-color: rgba(255,255,255,0.2);
}
.side-nav-item a {
    text-decoration: none;
    color: #909090;
}
.nav-icons {
    color: white;
    text-align: center;
    font-size: 25px;
}

