    height: 92vh;
    /* width: 1461px; */
    max-width: 95.1%;
    left: 75px;
    top: 60px;
    z-index: -2;
}
.main-container-cd {
    position: absolute;
    flex-direction: column;
    display: flex;
    height: 120vh;
    /* width: 1291px; */
    width: 83.95%;
    left: 245px;
    top: 60px;
    z-index: -2;
}
.chip-bar-container {
    display: flex;
    width: 95.1%;
    height: 60px;
    background-color: rgba(33, 33, 33, 0.985);
    color: white;
    position: fixed;
    border-bottom: 1px solid #5c5c5c7c;;
}
.chip-bar-container-cd {
    display: flex;
