.profile-navbar-container {
    position: absolute;
    display: inline-flex;
    flex-direction: column;
    height: 100vh;
    width: 300px;
    background-color: rgba(33, 33, 33, 0.985);
    color: white;
    right: 75px;
    top: 0;
    border-right: 1px solid #5c5c5c7c;
    border-left: 1px solid #5c5c5c7c;
}
.profile-header {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    padding: 0;
    margin-bottom: 0;
}
#profile-header-pic {
    height: 60px;
    width: 70px;
}
.profile-info {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.profile-text {
    justify-content: center;
}
.profile-text h3 {
    margin: 1px;
    font-size: 16px;
}
.profile-text h4 {
    margin: 1px;
    font-size: 14px;
    font-weight: lighter;
}
.manage-google-acc {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 14px;
}
.manage-google-acc a {
    text-decoration: none;
    color: #3EA6FF;
}
.main-section {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
}
.profile-nav-item {
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 100%;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
}
.profile-nav-item:hover {
    background-color: rgba(255, 255, 255, 0.2)
}
.profile-nav-icon {
    margin-right: 14px;
    margin-left: 14px;
    color: #909090;
    font-size: 25px;
}
.restricted {
    padding-left: 14px;
}
.expand-arrow {
    color: #909090;
    position: absolute;
    right: 10px;
}